export default {
  props: ["categories"],
  data: function data() {
    return {
      showMore: false
    };
  },
  methods: {
    categoryRecord: function categoryRecord(id, name, level) {
      localStorage.setItem('c_buryResource', 'category');

      if (level == 1) {
        this.$Burying({
          object: '5002',
          objectId: id,
          objectUrl: name
        });
      } else if (level == 2) {
        this.$Burying({
          object: '5003',
          objectId: id,
          objectUrl: name
        });
      }
    },
    moreChange: function moreChange() {
      this.showMore = !this.showMore;
    }
  }
};